.about {
  padding-top: 30px;
  padding-bottom: 70px;
  background-color: #202020;
}

.about__content {
  display: flex;
  flex-direction: column;
}

.about__content-camera-bg {
  position: relative;
}

.about__content-camera-bg::before {
  content: "";
  position: absolute;
  top: 33px;
  width: 100%;
  height: 64%;
  background-image: url(../img/camera-bg.png);
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
}

.about__title {
  margin-bottom: 10px;
}

.about__descr {
  color: #fff;
}

.about__form {
  margin-top: auto;
}

.about__form-title {
  margin-bottom: 8px;
}

.about__form input {
  width: 474px;
  margin-right: 26px;
  color: #cacaca;
  background-color: #4a4a4a;
}

.about__form button {
  border: none;
  cursor: pointer;
}

@font-face {
  font-family: "Roboto";
  src: local("Roboto-Bold"),url("../fonts/Roboto/Roboto-Bold.woff2") format("woff2"),url("../fonts/Roboto/Roboto-Bold.woff") format("woff");
  font-weight: 700;
}

@font-face {
  font-family: "Roboto";
  src: local("Roboto-Regular"),url("../fonts/Roboto/Roboto-Regular.woff2") format("woff2"),url("../fonts/Roboto/Roboto-Regular.woff") format("woff");
  font-weight: 400;
}

@font-face {
  font-family: "TT Firs Neue";
  src: local("TTFirsNeue-Bold"),url("../fonts/TT Firs Neue/TTFirsNeue-Bold.woff2") format("woff2"),url("../fonts/TT Firs Neue/TTFirsNeue-Bold.woff") format("woff");
  font-weight: 700;
}

@font-face {
  font-family: "TT Firs Neue";
  src: local("TTFirsNeue-Regular"),url("../fonts/TT Firs Neue/TTFirsNeue-Regular.woff2") format("woff2"),url("../fonts/TT Firs Neue/TTFirsNeue-Regular.woff") format("woff");
  font-weight: 400;
}

html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

img {
  max-width: 100%;
}

body {
  background-color: #fff;
  font-family: serif;
}

.container {
  max-width: 1352px;
  margin: 0 auto;
  padding: 0 31px;
}

.logo {
  width: 190px;
  height: 31px;
}

.logo img {
  object-fit: cover;
}

.orange-btn {
  padding: 16px 21px;
  font-family: "Roboto";
  font-weight: 700;
  color: #fff;
  background-color: #ff6e30;
  transition: background-color .1s ease-in-out;
}

.orange-btn:hover {
  background-color: #bc572b;
}

.orange-btn:focus {
  border: 3px solid #bc572b;
}

.orange-btn:active {
  border: none;
  background-color: #ce4307;
}

.hash {
  padding: 6px 14px;
  font-family: "Roboto";
  font-weight: 400;
  font-size: 14px;
  color: #fff;
  background-color: #202020;
}

.section-title-dark {
  font-family: "TTFirsNeue";
  font-size: 28px;
  font-weight: 700;
  line-height: 25px;
  color: #202020;
}

.section-title-light {
  font-family: "TTFirsNeue";
  font-size: 28px;
  font-weight: 700;
  line-height: 25px;
  color: #fff;
}

.card-title-dark {
  font-family: "TTFirsNeue";
  font-size: 28px;
  font-weight: 400;
  color: #202020;
}

.card-title-light {
  font-family: "TTFirsNeue";
  font-size: 28px;
  font-weight: 400;
  color: #fff;
}

.text-lh-28 {
  font-family: "Roboto";
  font-weight: 400;
  font-size: 16px;
  line-height: 28px;
}

.text-lh-22 {
  font-family: "Roboto";
  font-weight: 400;
  font-size: 16px;
  line-height: 22px;
}

.input {
  border: none;
  padding: 16px 20px;
  font-size: 16px;
}

.input:focus-visible {
  outline: none;
}

.input:focus {
  outline: 1px solid #ff6e30;
}

.input:hover::placeholder {
  color: #ff6e30;
}

.input:focus-visible {
  outline: none;
}

.input:invalid {
  outline: 1px solid #ff3030;
}

.data {
  display: flex;
  align-items: center;
  font-size: 12px;
  font-weight: 400;
}

.data svg {
  width: 14px;
  height: 14px;
  margin-right: 4px;
}

.tel {
  display: flex;
  align-items: center;
  color: #ff6e30;
  font-size: 16px;
}

.tel svg {
  width: 22px;
  height: 22px;
}

.contacts {
  padding-bottom: 71px;
}

.contacts__title {
  margin-bottom: 15px;
}

.contacts__grid {
  grid-template-rows: 470px;
}

.contacts__map {
  position: relative;
  padding-top: 10px;
}

.contacts__map-cover {
  position: absolute;
  overflow: hidden;
  top: 12px;
  left: 0;
  height: calc(100% - 10px);
  width: 410px;
  padding-top: 170px;
  padding-left: 60px;
  background-color: rgba(32,32,32,.85);
  z-index: 1000;
}

.contacts__map-close {
  position: absolute;
  top: 20px;
  left: 370px;
  width: 20px;
  height: 20px;
  cursor: pointer;
}

.contacts__map-close svg {
  width: 100%;
  height: 100%;
}

.contacts__map-content {
  max-width: 300px;
}

.contacts__map-address {
  font-style: normal;
  color: #cacaca;
}

.contacts__map-open {
  position: absolute;
  display: none;
  z-index: 1000;
  cursor: pointer;
  border: solid #ff6e30;
  border-width: 0 3px 3px 0;
  padding: 7px;
  transform: rotate(-45deg);
}

.contacts__request-descr {
  margin-bottom: 14px;
  color: #999;
}

.contacts__request-form {
  display: flex;
  flex-direction: column;
}

.contacts__request-form input {
  margin-bottom: 20px;
  background: #e9e9e9;
  color: #999;
}

.contacts__request-input-comment {
  height: 150px;
}

.contacts__request-btn {
  border: none;
  cursor: pointer;
  width: 126px;
}

[class*=layers-pane] {
  filter: grayscale(1);
}

.footer {
  background-color: #202020;
  padding-bottom: 17px;
  padding-top: 23px;
}

.footer__wrapper {
  display: flex;
  align-items: center;
}

.footer__logo {
  margin-bottom: 6px;
}

.footer__logo-wrapper {
  margin-right: 205px;
}

.footer__logo-descr {
  color: #e9e9e9;
  font-size: 16px;
  font-weight: 400;
  font-family: "TTFirsNeue";
}

.footer__descr {
  max-width: 361px;
}

.footer__descr p {
  color: #cacaca;
}

.footer__socials {
  margin-left: auto;
}

.footer__socials-title {
  color: #cacaca;
  display: block;
  margin-bottom: 8px;
}

.footer__list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
  justify-content: center;
}

.footer__item:not(:last-child) {
  margin-right: 20px;
}

.footer__item svg {
  fill: #cacaca;
  transition: fill .1s ease-in-out;
}

.footer__item svg:hover {
  fill: #ff6e30;
}

.footer__item svg:focus {
  fill: #bc572b;
}

.footer__item svg:active {
  fill: #ce4307;
}

.grid {
  display: grid;
  grid-template-columns: repeat(12, 1fr);
  gap: 30px;
}

.header {
  padding: 25px 0;
  background-color: #202020;
}

.header .container {
  display: flex;
  align-items: center;
}

.header__logo {
  margin-right: 140px;
}

.header__list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
  font-family: "TT Firs Neue";
  font-weight: 400;
}

.header__item:not(:last-child) {
  margin-right: 79px;
}

.header__item a {
  text-decoration: none;
  color: #fff;
  font-size: 16px;
  transition: color .1s ease-in-out;
}

.header__item a:hover {
  color: #ff6e30;
}

.header__item a:focus {
  background-color: #bc572b;
}

.header__item a:active {
  color: #ce4307;
  background-color: rgba(0,0,0,0);
}

.header__tel {
  display: none;
}

.header__search-form {
  position: relative;
  height: 25px;
  margin-left: auto;
}

.header__search-input {
  border: none;
  position: absolute;
  padding: 0;
  width: 0;
  visibility: hidden;
  height: 25px;
  background-color: #202020;
  z-index: 10;
  color: #fff;
  border-bottom: 1px solid #cacaca;
  transition: border-color .1s ease-in-out;
}

.header__search-input:focus-visible {
  outline: none;
}

.header__search-input:hover,
.header__search-input:focus {
  border-color: #ff6e30;
}

.header__search-input:focus-visible {
  border-color: #ce4307;
}

.header__search-input:focus-visible::placeholder {
  color: #e9e9e9;
}

.header button {
  border: none;
  cursor: pointer;
  height: 25px;
  width: 25px;
  padding: 0;
  background-color: rgba(0,0,0,0);
}

.header button svg {
  width: 100%;
  height: 100%;
}

.header__search-close {
  display: none;
  position: absolute;
  right: 0;
}

.header #open-burger {
  display: none;
}

.hero {
  padding-top: 41px;
  padding-bottom: 67px;
}

.hero__grid {
  grid-template-rows: 343px 257px;
}

.master-class {
  grid-row: span 2;
  position: relative;
  display: flex;
  padding-bottom: 64px;
  padding-left: 58px;
  padding-right: 20px;
  background-image: url(../img/hero-img.png);
  background-position: center;
  background-size: cover;
}

.master-class__content {
  margin-top: auto;
  max-width: 430px;
}

.master-class__author {
  display: block;
  font-family: "Roboto";
  font-weight: 400;
  font-size: 16px;
  color: #fff;
}

.master-class__title {
  margin-bottom: 19px;
  font-family: "TTFirsNeue";
  font-weight: 700;
  font-size: 42px;
  line-height: 37px;
  color: #fff;
}

.master-class__link {
  text-decoration: none;
  display: inline-block;
}

.master-class__hash {
  position: absolute;
  top: 15px;
  right: 15px;
}

.work-example__img {
  width: 100%;
  height: 100%;
  margin: 0;
}

.work-example__img img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.work-example__title {
  margin-bottom: 17px;
}

.projects {
  padding-top: 68px;
  padding-bottom: 68px;
  padding: 70px 0;
}

.projects__title {
  margin-bottom: 16px;
}

.projects__grid {
  grid-template-rows: repeat(2, 300px);
}

.card {
  position: relative;
}

.card__hash {
  position: absolute;
  top: 7px;
  right: 7px;
}

.card__descr {
  color: #202020;
}

.card img {
  margin-bottom: 14px;
  width: 100%;
  height: 184px;
}

.card__data {
  position: absolute;
  left: 0;
  bottom: 5px;
  color: #999;
}

.card-with-bg {
  position: relative;
  padding-top: 26px;
  padding-left: 30px;
  padding-bottom: 30px;
}

.card-with-bg__content {
  position: relative;
  max-width: 291px;
  height: 100%;
}

.card-with-bg__data {
  margin-bottom: 6px;
  color: #fff;
}

.card-with-bg__hash {
  position: absolute;
  top: 7px;
  right: 7px;
  margin-bottom: 2px;
}

.card-with-bg__title {
  margin-bottom: 6px;
  line-height: 24px;
}

.card-with-bg__descr {
  color: #fff;
}

.card-with-bg__link {
  text-decoration: none;
  position: absolute;
  left: 0;
  bottom: 0;
  font-family: "Roboot";
  font-size: 16px;
  font-weight: 700;
  padding: 13px 22px;
  border: 3px solid #ff6e30;
  color: #fff;
  transition: background-color .1s ease-in-out,border-color .1s ease-in-out;
}

.card-with-bg__link:hover {
  background-color: #ff6e30;
}

.card-with-bg__link:focus {
  background-color: #bc572b;
}

.card-with-bg__link:active {
  border-color: #ce4307;
  background-color: #ce4307;
}

.fuji-bg {
  background-image: url(../img/fuji.png);
  background-size: cover;
  background-repeat: no-repeat;
}

.oblivion-bg {
  background-image: url(../img/oblivion-project.png);
  background-size: cover;
  background-repeat: no-repeat;
}

@media (max-width: 1350px) {
  .about__form input {
    width: 342px;
  }
}

@media (max-width: 1200px) {
  .about {
    padding-bottom: 90px;
  }

  .about__content-camera-bg::before {
    height: 62%;
  }

  .about__title {
    margin-bottom: 10px;
  }

  .about__form-title {
    margin-bottom: 10px;
  }

  .about__form input {
    margin-bottom: 12px;
  }

  .logo {
    width: 166px;
    height: 27px;
  }

  .tel svg {
    width: 16px;
    height: 16px;
  }

  .contacts {
    padding-bottom: 68px;
  }

  .contacts__grid {
    grid-template-rows: 470px;
  }

  .contacts__map-cover {
    width: 100%;
    height: 158px;
    top: auto;
    bottom: 0;
    padding-top: 20px;
    padding-left: 27px;
  }

  .contacts__map-close {
    left: auto;
    right: 20px;
  }

  .contacts__map-address {
    position: relative;
  }

  .contacts__map-tel {
    position: absolute;
    right: -7px;
    bottom: 0px;
  }

  .contacts__request-title {
    line-height: 30px;
  }

  .footer__logo-wrapper {
    margin-right: 135px;
  }

  .grid {
    grid-template-columns: repeat(8, 1fr);
  }

  .header {
    padding: 25px 0;
  }

  .header__item:not(:last-child) {
    margin-right: 33px;
  }

  .header__logo {
    margin-right: 80px;
  }

  .hero {
    padding-top: 32px;
    padding-bottom: 68px;
  }

  .hero__grid {
    grid-template-rows: 409px 186px;
  }

  .master-class {
    padding-left: 28px;
    padding-bottom: 28px;
  }

  .master-class__author {
    margin-bottom: 14px;
  }

  .master-class__title {
    font-size: 40px;
    line-height: 30px;
    margin-bottom: 28px;
  }

  .work-example__title {
    margin-bottom: 27px;
  }

  .projects__grid {
    row-gap: 24px;
    grid-template-rows: 285px repeat(2, 307px);
  }

  .card img {
    margin-bottom: 13px;
  }

  .card-with-bg {
    padding-bottom: 38px;
    padding-top: 34px;
  }

  .col-end {
    order: 6;
  }
}

@media (max-width: 970px) {
  .about {
    padding-bottom: 63px;
  }

  .about__form {
    padding-bottom: 7px;
  }

  .about__form input {
    margin-right: 0;
    margin-bottom: 20px;
  }

  .container {
    padding: 0 27px;
  }

  .section-title-dark {
    font-size: 26px;
    line-height: 20px;
  }

  .contacts {
    padding-bottom: 67px;
  }

  .contacts__title {
    margin-bottom: 20px;
  }

  .contacts__grid {
    row-gap: 20px;
  }

  .contacts__map {
    padding: 0;
  }

  .contacts__map-content {
    max-width: 436px;
  }

  .contacts__map-tel {
    position: initial;
    font-size: 18px;
  }

  .footer {
    padding-bottom: 18px;
  }

  .footer__logo-wrapper {
    margin: 0;
  }

  .footer__descr {
    display: none;
  }

  .grid {
    grid-template-columns: repeat(6, 1fr);
  }

  .header {
    position: relative;
  }

  .header__logo {
    margin-right: 0;
  }

  .header__nav {
    position: absolute;
    top: 100%;
    left: 0;
    background-color: #4a4a4a;
    width: 100%;
    padding: 13px 27px;
  }

  .header__list {
    justify-content: space-between;
  }

  .header__item:not(:last-child) {
    margin-right: 0;
  }

  .hero {
    padding-top: 81px;
  }

  .hero__grid {
    grid-template-rows: 289px 133px;
    grid-template-columns: repeat(8, 1fr);
  }

  .work-example__title {
    margin-bottom: 21px;
  }

  .projects {
    padding-bottom: 73px;
  }

  .projects__grid {
    row-gap: 26px;
    grid-template-rows: repeat(2, 300px) 307px 300px;
  }

  .projects__title {
    margin-bottom: 20px;
  }

  .card-with-bg {
    padding-bottom: 31px;
  }
}

@media (max-width: 800px) {
  .about__content-camera-bg::before {
    background-image: url(../img/camera-bg-342x569.png);
    height: 70%;
  }

  .about__form input {
    width: 100%;
  }
}

@media (max-width: 600px) {
  .about__descr {
    font-size: 14px;
    line-height: 25px;
  }

  .container {
    padding: 0 21px;
  }

  .logo {
    width: 112px;
    height: 18px;
  }

  .hash {
    font-size: 12px;
  }

  .orange-btn {
    padding: 11px 23px;
    font-size: 14px;
  }

  .section-title-dark,
  .section-title-light {
    font-size: 21px;
    line-height: 22px;
  }

  .card-title-dark,
  .card-title-light {
    font-size: 21px;
    line-height: 22px;
  }

  .input {
    padding: 12px 23px;
  }

  .header {
    padding: 22px 0;
  }

  .header__nav {
    height: 45px;
    padding-top: 14px;
    padding-left: 21px;
    padding-bottom: 13px;
  }

  .header__list {
    z-index: 100;
    position: absolute;
    flex-direction: column;
    justify-content: normal;
    height: 100vh;
    width: 100%;
    left: 0;
    top: -1px;
    padding-top: 60px;
    padding-left: 21px;
    padding-bottom: 36px;
    background-color: #202020;
  }

  .header__tel {
    z-index: 1000;
    position: absolute;
    display: block;
    top: calc(100vh - 136px);
  }

  .header__burger-container {
    position: relative;
    z-index: 1000;
    height: 100%;
    width: 20px;
    display: flex;
    align-items: center;
    cursor: pointer;
  }

  .header__burger {
    position: relative;
    width: 100%;
    height: 2px;
    background-color: #fff;
    transition: transform .2s ease-in-out;
  }

  .header__burger::before,
  .header__burger::after {
    content: "";
    position: absolute;
    height: 2px;
    background-color: #fff;
    transition: transform .2s ease-in-out;
  }

  .header__burger::before {
    top: -6px;
    width: 100%;
  }

  .header__burger::after {
    top: 6px;
    width: 50%;
  }

  #open-burger:checked~.header__burger-container {
    padding: 3px;
    background-color: #4a4a4a;
    border-radius: 50%;
  }

  #open-burger:checked+.header__burger-container .header__burger::after {
    display: none;
  }

  #open-burger:checked+.header__burger-container .header__burger::before {
    top: 0;
    transform: rotate(90deg);
  }

  #open-burger:checked+.header__burger-container .header__burger {
    transform: rotate(-45deg) scale(0.75);
  }

  #open-burger~.header__list {
    opacity: 0;
    transform: translateX(-100%);
    transition: opacity .25s ease-in-out,transform .3s ease-in-out;
  }

  #open-burger~.header__tel {
    transform: translateX(-100vw);
    opacity: 0;
    transition: opacity .25s ease-in-out,transform .3s ease-in-out;
  }

  #open-burger:checked~.header__list {
    opacity: 1;
    transform: translateX(0);
  }

  .header #open-burger:checked~.header__tel {
    opacity: 1;
    transform: translateX(0);
  }

  .header__item:not(:last-child) {
    margin-bottom: 33px;
  }

  .header__search-form button {
    width: 20px;
    height: 20px;
  }

  .hero {
    padding-top: 76px;
  }

  .master-class__hash {
    padding: 4px 7px;
    top: 6px;
    right: 5px;
  }

  .master-class__title {
    font-size: 28px;
    line-height: 26px;
  }

  .master-class__author {
    font-size: 14px;
  }
}

@media (max-width: 520px) {
  .about .container .grid {
    gap: 18px;
  }

  .about__content-camera-bg::before {
    background-image: url(../img/camera-bg-278x278.png);
    height: 278px;
    top: 0;
  }

  .about__content.about__content-camera-bg {
    height: 440px;
  }

  .about__form {
    padding: 0;
  }

  .contacts__grid {
    grid-template-rows: auto;
  }

  .contacts__map {
    width: calc(100% + 42px);
    margin-left: -21px;
  }

  .contacts__map-title {
    margin-bottom: 10px;
  }

  .contacts__map-content {
    max-width: 270px;
  }

  .contacts__map-address {
    font-size: 14px;
    line-height: 18px;
  }

  .contacts__map-tel {
    font-size: 14px;
    display: block;
    margin-top: 13px;
  }

  .footer {
    padding-bottom: 0;
  }

  .footer__wrapper {
